<template>
    <loading v-model:active="query.isLoading"
        :can-cancel="true"
        :on-cancel="onCancel"
        :is-full-page="query.fullPage"/>
    <div>
        <h2>
            <router-link to="/">最新消息</router-link>
        </h2>
        <table class="table table-hover">
            <thead>
                <tr>
                    <th scope="col" colspan="2">標題</th>
                    <th scope="col" class="text-end">發布日期</th>
                    <th scope="col" class="text-end">閱覽次數</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in news" :key="index">
                    <th scope="row">
                        <span v-if="item.notification.id == 1" class="badge bg-primary">{{ item.notification.type }}</span>
                        <span v-if="item.notification.id == 2" class="badge bg-secondary">{{ item.notification.type }}</span>
                        <span v-if="item.notification.id == 3" class="badge bg-warning text-dark">{{ item.notification.type }}</span>
                        <span v-if="item.notification.id == 4" class="badge bg-info text-dark">{{ item.notification.type }}</span>
                        <span v-if="item.notification.id == 5" class="badge bg-danger">{{ item.notification.type }}</span>
                    </th>
                    <td>
                        <router-link :to="'/news/'+item.id">
                            <span v-if="item.top">【置頂】</span>
                            {{ item.title }}
                        </router-link>
                    </td>
                    <td class="text-end">{{ moment(item.created_at).format("YYYY-MM-DD HH:MM") }}</td>
                    <td class="text-end">{{ item.views }}</td>
                </tr>
            </tbody>
        </table>
        <nav aria-label="Page navigation example">
            <ul class="pagination justify-content-end">
                <li class="page-item disabled" v-if="page.current == 1">
                    <a @click="prevPage" class="page-link">Previous</a>
                </li>
                <li class="page-item" v-if="page.current != 1">
                    <a @click="prevPage" class="page-link">Previous</a>
                </li>
                <li class="page-item" v-for="index in rows" :key="index">
                    <a v-if="page.current == index" class="page-link bg-primary text-light fw-bolder" @click="locationPage(index)">{{ index }}</a>
                    <a v-if="page.current != index" class="page-link" @click="locationPage(index)">{{ index }}</a>
                </li>
                <li class="page-item disabled" v-if="page.current == page.all">
                    <a @click="nextPage" class="page-link">Next</a>
                </li>
                <li class="page-item" v-if="page.current != page.all">
                    <a @click="nextPage" class="page-link">Next</a>
                </li>
            </ul>
        </nav>
    </div>
</template>

<script>
import moment from 'moment';
import { NewsServer } from "../../providers/http-server.js";
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

export default {
    name: 'News',
    data(){
        return {
            news: [],
            page: {
                current: 1,
                all: 1
            },
            query: {
                isLoading: true,
                fullPage: true
            }
        }
    },
    components: {
        Loading
    },
    created(){
        this.moment = moment;
        NewsServer.read().then(
            (response) => {
                this.news = response.data.result;
                this.page.current = response.data.pages.current;
                this.page.all = response.data.pages.all;
                this.query.isLoading = false;
            }
        );
    },
    computed: {
        rows(){
            return this.page.all
        }
    },
    methods:
    {
        prevPage() {
            this.page.current -= 1;
            NewsServer.read({
                page: this.page.current
            }).then(
                (response) => {
                    this.news = response.data.result;
                    this.editing.data = this.news[0];
                    this.editing.notification = this.news[0].notification;
                    this.page.current = response.data.pages.current;
                    this.page.all = response.data.pages.all;
                }
            );
        },
        nextPage()
        {
            this.page.current += 1;
            NewsServer.read({
                page: this.page.current
            }).then(
                (response) => {
                    this.news = response.data.result;
                    this.editing.data = this.news[0];
                    this.editing.notification = this.news[0].notification;
                    this.page.current = response.data.pages.current;
                    this.page.all = response.data.pages.all;
                }
            );
        },
        locationPage(num)
        {
            this.current = num;
            NewsServer.read({
                page: num
            }).then(
                (response) => {
                    this.news = response.data.result;
                    this.editing.data = this.news[0];
                    this.editing.notification = this.news[0].notification;
                    this.page.current = response.data.pages.current;
                    this.page.all = response.data.pages.all;
                }
            );
        }
    }
}
</script>

