import axios from "axios";

// 伺服器請求時間上限
axios.defaults.timeout = 10000;
// 域名設定
axios.defaults.baseURL = "http://localhost/ntcust_lang_backend/public/";

axios.interceptors.request.use(
    config => {
        config.data = JSON.stringify(config.data);
        // 如果沒有cors的問題則可以都不加
        config.headers = {
            'Content-Type': 'application/json',
            "Accept": "application/json",
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Methods": "GET, PUT, POST, DELETE, OPTIONS",
            "Access-Control-Max-Age": "86400",
            "Authorization": 'Bearer ' + localStorage.getItem('API_TOKEN'),
        };
        return config;
    },
    error => {
        return Promise.reject(error);
    }
);

// 異常處理
axios.interceptors.response.use(
    response => {
        // console.clear()
        return response;
    },
    err => {
        // console.clear()
        if (err && err.response) {
            switch (err.response.status) {
                case 400: console.log("使用者端請求資料格式錯誤、或者有遺漏"); break;
                case 401: console.log("無效的輸入資料，或資料已存在、已刪除"); break;
                case 403: console.log("使用者之權限不足或已被停權"); break;
                case 404: console.log("找不到該資料或該頁面"); break;
                case 419: console.log("CORS請求問題"); break;
                case 429: console.log("過多請求在同時間(Too Many Requests)"); break;
                case 500: console.log("伺服器出錯，請稍後再試試"); break;
                case 503: console.log("伺服器停止運作"); break;
            }
        } else {
            console.log("連接到服務器失敗");
        }
        Promise.resolve(err.response);
    }
);

// 讀取
export function read(url, params = {}) {
    return new Promise((resolve, reject) => {
        axios
            .get(url, {
                params: params
            })
            .then(response => {
                resolve(response.data);
            })
            .catch(err => {
                reject(err);
            });
    });
}

// 新增
export function create(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.post(url, data).then(
            response => {
                resolve(response.data);
            },
            err => {
                reject(err);
            }
        );
    });
}

// 移除
export function remove(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.delete(url, data).then(
            response => {
                resolve(response.data);
            },
            err => {
                reject(err);
            }
        );
    });
}

// 更新
export function update(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.post(url, data).then(
            response => {
                resolve(response.data);
            },
            err => {
                reject(err);
            }
        );
    });
}

// 置頂
export function set(url, data = {}) {
    return new Promise((resolve, reject) => {
        axios.put(url, data).then(
            response => {
                resolve(response.data);
            },
            err => {
                reject(err);
            }
        );
    });
}

// 將封裝的方法打包起來 命名 NewsServer
export const NewsServer = {
    read: function (paramObj) {
        return read("api/news", paramObj);
    },
    item: function (id, paramObj) {
        return read("api/news/" + id, paramObj);
    },
    readALL: function (paramObj) {
        return read("api/news/all", paramObj);
    },
    create: function (paramObj) {
        return create("api/news/create", paramObj);
    },
    update: function (id, paramObj) {
        return update("api/news/update/" + id, paramObj);
    },
    top: function (id, paramObj) {
        return set("api/news/top/" + id, paramObj);
    },
    softdelete: function (id, paramObj) {
        return remove("api/news/softdelete/" + id, paramObj);
    },
    delete: function (id, paramObj) {
        return remove("api/news/delete/" + id, paramObj);
    }
};

