<template>

    <loading v-model:active="query.isLoading"
        :can-cancel="true"
        :on-cancel="onCancel"
        :is-full-page="query.fullPage"/>
    <h2><router-link to="/">榮譽榜</router-link></h2>
    <table class="table table-hover">
        <thead>
            <tr>
                <th scope="col" class="text-center">
                    科系
                </th>
                <th scope="col">標題</th>
                <th scope="col" class="text-center">排名</th>
                <th scope="col" class="text-center">發布日期</th>
                <th scope="col" class="text-center">更新日期</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in honor" :key="index">
                <td class="text-center fs-5">
                    <b-badge v-if="item.department == 41" pill variant="dark">應用英文系</b-badge>
                    <b-badge v-if="item.department == 42" pill variant="dark">應用日文系</b-badge>
                    <b-badge v-if="item.department == 43" pill variant="dark">應用中文系</b-badge>
                </td>
                <td>{{ item.title }}</td>
                <td class="text-center">
                    <span v-if="item.ranking_style == 1" class="badge rounded-pill bg-warning text-dark">
                        {{ item.ranking }}
                    </span>
                    <span v-if="item.ranking_style == 2" class="badge rounded-pill bg-light text-dark">
                        {{ item.ranking }}
                    </span>
                    <span v-if="item.ranking_style == 3" class="badge rounded-pill bg-secondary">
                        {{ item.ranking }}
                    </span>
                    <span v-if="item.ranking_style == 0" class="badge rounded-pill bg-primary">
                        {{ item.ranking }}
                    </span>
                </td>
                <td class="text-center">{{ moment(item.created_at).format("YYYY-MM-DD HH:MM") }}</td>
                <td class="text-center">{{ moment(item.updated_at).format("YYYY-MM-DD HH:MM") }}</td>
            </tr>
        </tbody>
    </table>
    <nav aria-label="Page navigation example">
        <ul class="pagination justify-content-end">
            <li class="page-item disabled" v-if="page.current == 1">
                <a @click="prevPage" class="page-link">Previous</a>
            </li>
            <li class="page-item" v-if="page.current != 1">
                <a @click="prevPage" class="page-link">Previous</a>
            </li>
            <li class="page-item" v-for="index in rows" :key="index">
                <a v-if="page.current == index" class="page-link bg-primary text-light fw-bolder" @click="locationPage(index)">{{ index }}</a>
                <a v-if="page.current != index" class="page-link" @click="locationPage(index)">{{ index }}</a>
            </li>
            <li class="page-item disabled" v-if="page.current == page.all">
                <a @click="nextPage" class="page-link">Next</a>
            </li>
            <li class="page-item" v-if="page.current != page.all">
                <a @click="nextPage" class="page-link">Next</a>
            </li>
        </ul>
    </nav>
</template>

<script>
import moment from 'moment';
import { HonorServer } from "../../providers/http-server.js";

import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

export default {
    name: 'App',
    data() {
        return {
            honor: [],
            page: {
                current: 1,
                all: 1
            },
            query: {
                isLoading: true,
                fullPage: true
            }
        }
    },
    components: {
        Loading
    },
    created() {
        this.moment = moment;
        HonorServer.read().then(
            (response) => {
                this.honor = response.data;
                this.page.all = Math.ceil(response.data.length / 10);
                this.query.isLoading = false;
            }
        );
    },
    computed: {
        rows(){
            return this.page.all
        }
    }
};
</script>

<style scoped></style>