<template>
    <div class="vld-parent">
        <loading v-model:active="query.isLoading"
            :can-cancel="true"
            :on-cancel="onCancel"
            :is-full-page="query.fullPage"/>
    </div>

    <h2><router-link to="/">活動花絮</router-link></h2>
    <div class="row">
        <div class="col-4" v-for="(item, index) in albums.data" :key="index">
            <router-link :to="'/album/'+item.id">
            <div>
                <b-card
                    overlay
                    fluid-grow
                    :img-src="'http://localhost/ntcust_lang_backend/storage/app/' + item.cover.path"
                    img-alt="Card Image"
                    text-variant="dark"
                    :title="item.name"
                >
                    <b-card-text>
                        <!-- <b-alert show variant="secondary" style="opacity: .5">{{ item.description }}</b-alert> -->
                    </b-card-text>
                </b-card>
            </div>
            </router-link>
        </div>
    </div>
</template>

<script>
import moment from 'moment';
import { AlbumServer } from "../../providers/http-server.js";
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

export default {
    name: 'App',
    components: {
        Loading
    },
    data()
    {
        return {
            albums: {
                data: []
            },
            query: {
                isLoading: true,
                fullPage: true
            }
        }
    },
    created()
    {
        this.moment = moment;
        AlbumServer.read().then((response) => {
            this.albums.data = response.data;
            this.query.isLoading = false;
        });
    }
};
</script>

