<template>
    <loading v-model:active="query.isLoading"
        :can-cancel="true"
        :on-cancel="onCancel"
        :is-full-page="query.fullPage"/>
    <carousel :items-to-show="1" :wrapAround="true" :autoplay="5000" :transition="300">
        <slide v-for="slide in banners" :key="slide">
            <img :src="'http://localhost/ntcust_lang_backend/storage/app/' + slide.file.path" 
                style="max-height: 600px; width:100%;"
                :alt="slide.title"
                :title="slide.title">
        </slide>

        <template #addons>
            <navigation />
            <pagination />
        </template>
    </carousel>
</template>

<script>
    import 'vue3-carousel/dist/carousel.css';
    import { Carousel, Slide, Pagination, Navigation } from 'vue3-carousel';
    import { BannerServer } from "../../providers/http-server.js";
    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';

    export default {
        name: 'App',
        data()
        {
            return {
                banners: [],
                query: {
                    isLoading: true,
                    fullPage: true
                }
            }
        },
        components: {
            Carousel,
            Slide,
            Pagination,
            Navigation,
            Loading
        },
        created()
        {
            BannerServer.read().then(
                (response) => {
                    this.banners = response.data;
                    this.query.isLoading = false;
                }
            );
        }
    };
</script>
<style scoped></style>