import { createRouter, createMemoryHistory, createWebHistory } from 'vue-router';
import Home from '../views/Home.vue';

const isServer = typeof window === 'undefined';
const history = isServer ? createMemoryHistory() : createWebHistory();
const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home,
    },
    {
        path: '/album/:id',
        name: 'Album',
        component: () => import('../views/pages/AlbumDetail.vue')
    },
    {
        path: '/news/:id',
        name: 'News',
        component: () => import('../views/pages/NewsDetail.vue')
    },
    {
        path: '/member',
        name: 'Member',
        component: () => import('../views/pages/Member.vue')
    },
    {
        path: '/regulation',
        name: 'Regulation',
        component: () => import('../views/pages/Regulation.vue')
    },
    {
        path: '/intro',
        component: () => import('../views/intro/home.vue'),
        children: [
            {
                path: '',
                component: () => import('../views/intro/zhTW.vue'),
            },
            {
                path: 'zhTW',
                component: () => import('../views/intro/zhTW.vue'),
            },
            {
                path: 'zhCN',
                component: () => import('../views/intro/zhCN.vue'),
            },
            {
                path: 'en',
                component: () => import('../views/intro/en.vue'),
            },
            {
                path: 'jp',
                component: () => import('../views/intro/jp.vue'),
            },
        ]
    },
    {
        path: '/link',
        name: 'Link',
        component: () => import('../views/pages/Link.vue')
    },
    {
        path: '/banner',
        name: 'Banner',
        component: () => import('../views/components/Banner.vue')
    },
    {
        path: '/manage',
        name: 'Manage',
        component: () => import('../views/pages/Manage.vue')
    },
    {
        path: '/map',
        name: 'Map',
        component: () => import('../views/pages/Map.vue')
    },
    {
        path: '/description',
        name: 'Description',
        component: () => import('../views/pages/Description.vue')
    },
    {
        path: '/login',
        component: () => import('../views/pages/Search.vue')
    },
    {
        path: '/search/:keyword?',
        name: 'Search',
        component: () => import('../views/pages/Search.vue')
    },
    {
        path: '/program',
        name: 'Program',
        component: () => import('../views/pages/Program.vue')
    },
    {
        path: '/quality',
        name: 'Quality',
        component: () => import('../views/pages/Quality.vue')
    },
    {
        path: '/article',
        name: 'Article',
        component: () => import('../views/pages/Article.vue')
    },
    {
        path: "/:pathMatch(.*)",
        component: () => import('../views/pages/404.vue')
    }
];

const router = createRouter({
    history,
    routes,
});

export default router;
